
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import tools from '../../lib/tools.js';


export default {
name:'toggleAccount',
components:{Group,Flexbox, FlexboxItem,Toast,XInput,Popup,Selector,XButton,Swipeout, SwipeoutItem, SwipeoutButton,XDialog},
directives: {TransferDom},
data(){
return {
peoples:[],
itemRadioBoxImgChecked:'itemRadioBoxImgChecked',
itemRadioBoxImg:'itemRadioBoxImg',
id:'',
roleId:'1',
toastMsg:'',
showToast:false,
showPopup:false,
roleOptions:[],
isParent:true,
showDialog:false,
showDialog2:false,
codeText:'发送验证码',
accountName:'',
accountPhone:'',
accountCode:'',
updateAccountId:'',
updateAccountMobile:'',
isDisabled:false,
num:59
}
},
mounted(){
this.loading();
this.loadRoleOption();
},
methods:{
loading(){
var vm = this;
let id = (localStorage.getItem('md') || tools.getRequestParam('managerId') || tools.getCookie('user_id')); // md===userid
if(!id){
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
return false;
}
this.id = id;
this.$http.post('/apiUser/querySubAccount?managerId='+id,{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200) && data.resultCode){
if(data.data){
for(let i=0;i<data.data.length;i++){
data.data[i].isActive = false;
if(data.data[i].managerId == id){
data.data[i].isActive = true;
if(data.data[i].parentId){
vm.isParent = false;
}
}
if(!data.data[i].parentId){
data.data[i].roleName = '主账号';
}
}
vm.peoples = data.data;
console.log(vm.peoples);

}else{
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
}
}else{
console.log(data.resultCode);
}
},(error) => {
console.log(error);
});
},
loadRoleOption(){
var vm = this;
let id = (localStorage.getItem('md') || tools.getRequestParam('managerId') || tools.getCookie('user_id')); // md===userid
if(!id){return false;}
this.id = id;
this.$http.post('/apiUser/queryRole',{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200) && data.resultCode){
if(data.data){
let tempArr = [];
for(let i=0;i<data.data.length;i++){
let item = data.data[i];
tempArr.push({key:item.id,value:item.itemName})
}
vm.roleOptions = tempArr;
}
}else{
console.log(data.resultCode);
}
},(error) => {
console.log(error);
});
},
toggleFn(user){
if(user.mobile){
this.radioFn(user.managerId)
}else{
this.updateAccountId = user.managerId;
this.updateAccountMobile = user.mobile;
this.showDialog2 = true;
}
},
radioFn(id){
for(let i=0;i<this.peoples.length;i++){
this.peoples[i].isActive = false;
}
this.$http.axios({method:'get',url:'/apiUser/changeAccount?managerId='+id}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
for(let i=0;i<this.peoples.length;i++){
let item = this.peoples[i];
if(item.managerId == id){
this.peoples[i].isActive = !this.peoples[i].isActive;
localStorage.removeItem('md');
tools.setCookie('user_id',item.managerId);
sessionStorage.removeItem('tempURL');
sessionStorage.removeItem('md');
localStorage.setItem('md',item.managerId);
this.showToast = true;
this.toastMsg = '切换成功!';
setTimeout(function(){
tools.next('/m/personal.html');
},800);
}
}
}else{
console.log(data.resultCode);
this.showToast = true;
this.toastMsg = data.resultCode.message;
}
},(error) => {
console.log(error);
this.showToast = true;
this.toastMsg = '切换失败';
})
},
saveAccount(){
var vm = this;
console.log(this.roleId,this.id,this.accountName);
if(!this.id){
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
return false;
}
if(!(this.accountName && this.roleId)){
vm.showToast = true;
vm.toastMsg = '请将角色信息输入完整!';
return false;
}
if(this.accountPhone){
if(!this.accountCode){
vm.showToast = true;
vm.toastMsg = '请正确输入手机认证信息!';
return false;
}
}else if(this.accountCode){
if(!this.accountPhone){
vm.showToast = true;
vm.toastMsg = '请正确输入手机认证信息!';
return false;
}
}
if(this.accountPhone && this.accountCode){
if(!(/^1(3|4|5|7|8)\d{9}$/.test(this.accountPhone))){
vm.showToast = true;
vm.toastMsg = '请正确输入手机号!';
return false;
}
}









let body = {managerId:vm.id,name:vm.accountName,roleId:vm.roleId,mobile:vm.accountPhone,randCode:vm.accountCode};
this.$http.axios({method:'POST',url:'/apiUser/addSubAccount',data:body}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200) && data.resultCode){
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
vm.showDialog = false;
vm.showDialog2 = false;
vm.accountCode = '';
vm.loading();
vm.loadRoleOption();
}else{
console.log(data.resultCode);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '添加失败';
})
},
deleteAccount(id,parentId){
var vm = this;
if(!id){return false;}
if(id == (localStorage.getItem('md') || tools.getRequestParam('md') || tools.getCookie('user_id'))){
vm.showToast = true;
vm.toastMsg = '无法删除当前登录人';
return false;
}
if(!parentId){
vm.showToast = true;
vm.toastMsg = '无法删除主账户';
return false;
}
this.$http.post('/apiUser/delSubAccount?managerId='+id,{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200) && data.resultCode){
vm.showToast = true;
vm.toastMsg = '删除成功';
vm.loading();
}else{
console.log(data.resultCode);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '删除失败';
});
},
accountCodeFn(){
if(this.isDisabled){
return ;
}
var vm = this;
if(!(/^1(3|4|5|7|8)\d{9}$/.test(this.accountPhone))){
this.showToast = true;
this.toastMsg = '手机号输入有误,请重新输入';
return ;
}







if(this.peoples.filter(item => {
console.log(item.mobile,this.accountPhone);
return item.mobile == this.accountPhone;
}).length>0){
vm.showToast = true;
vm.toastMsg = '输入号码已存在';
return false;
}

this.isDisabled = true;

this.$http.axios({method:'get',url:'/apiUser/updateRandCode?mobile='+this.accountPhone}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
this.showToast = true;
this.toastMsg = data.resultCode.message;
this.codeText = '60秒后可重发';
let timer = setInterval(() => {
if(this.num==0){
this.codeText = '发送验证码';
this.isDisabled = false;
this.num = 59;
clearInterval(timer);
return ;
}
this.codeText = this.num+'秒后可重发';
this.num--;
},1000)
}else{
console.log(data.resultCode);
this.showToast = true;
this.toastMsg = data.resultCode.message;
}
},(error) => {
console.log(error);
this.showToast = true;
this.toastMsg = '发送失败';
})
},
updateInfo(){
var vm = this;

if(this.accountPhone){
if(!this.accountCode){
vm.showToast = true;
vm.toastMsg = '请正确输入手机认证信息!';
return false;
}
}else if(this.accountCode){
if(!this.accountPhone){
vm.showToast = true;
vm.toastMsg = '请正确输入手机认证信息!';
return false;
}
}
if(!(/^1(3|4|5|7|8)\d{9}$/.test(this.accountPhone))){
vm.showToast = true;
vm.toastMsg = '请正确输入手机号!';
return false;
}

if(this.peoples.filter(item => {
console.log(item.mobile,this.accountPhone);
return item.mobile == this.accountPhone;
}).length>0){
vm.showToast = true;
vm.toastMsg = '输入号码已存在';
return false;
}

if(!this.accountCode){
vm.showToast = true;
vm.toastMsg = '请正确输入验证码!';
return false;
}

let data = {managerId:vm.updateAccountId,mobile:vm.accountPhone,randCode:this.accountCode};
this.$http.axios({method:'POST',url:'/apiUser/update',data:data}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
vm.accountCode = '';
vm.radioFn(vm.updateAccountId);
vm.showDialog = false;
vm.showDialog2 = false;
}else{
console.error(data);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '操作失败';
})
},
toggleUser(){
this.showDialog2 = false;
this.radioFn(this.updateAccountId);
}
}
}



